import React, { useState, useEffect, Suspense, useRef } from 'react';
import { useDispatch, connect } from 'react-redux';
import { useNavigate } from 'react-router-dom'
import { Helmet } from 'react-helmet'
import { getFixedT } from 'i18next';
import vertPng from './../../../assets/images/100g_vert.png'
import menuSvg from './../../../assets/images/menu.svg'

import { MdFavorite, MdFavoriteBorder, MdThumbDown, MdOutlineThumbDown } from "react-icons/md";

import { useGetProductsMutation, useAddFavouriteMutation, useAddUnlikeMutation, useAddCartMutation, useRemoveCartMutation, useSendUserReportMutation, useSendMyGroceryListMutation } from '../slices/membreApiSlice';
import Loading from '../../../components/Loading';
import { SET_SUBCATEGORY_FILTER, SET_SOURCE_FILTER, SET_KEYWORD_FILTER, ADD_REMOVE_FAVOURITE, ADD_REMOVE_UNLIKE, ADD_REMOVE_CART } from '../../../redux/constants/actionTypes';
import HighLightText from '../../../components/HighLightText';
import MobileExpandedMenu from '../../../components/MobileExpandedMenu';

const mapStateToProps = (state) => ({
    categories: state.product.categories,
    favourites: state.product.favourites,
    unlikes: state.product.unlikes,
    sources: state.product.sources,
    carts: state.product.carts,
    selectedCategory: state.filter.category,
    selectedSubCategory: state.filter.subCategory,
    selectedSources: state.filter.sources,
    keyword: state.filter.keyword,
});

const MobileMembre = ({ categories, favourites, unlikes, sources, carts, selectedCategory, selectedSubCategory, selectedSources, keyword }) => {
    const t = getFixedT(null, null, 'member')
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(0);
    const [results, setResults] = useState([]);
    const [filterKeyword, setFilterKeyword] = useState(keyword)
    const [menuShow, setMenuShow] = useState(false)
    const [submenuShow, setSubmenuShow] = useState(false)

    const [getProducts] = useGetProductsMutation()
    const [addFavourite] = useAddFavouriteMutation()
    const [addUnlike] = useAddUnlikeMutation()
    const [addCart] = useAddCartMutation()
    const [removeCart] = useRemoveCartMutation()

    const navigate = useNavigate()

    let totalCartNum = 0;
    for (let key in carts) {
        totalCartNum += carts[key].length
    }

    const dispatch = useDispatch();

    useEffect(() => {
        handleApply()
    }, [selectedCategory, selectedSources, selectedSubCategory, currentPage, keyword]);


    const handlePageChange = (page) => {
        setCurrentPage(page);
        // handleApply();
    };

    const maxPages = 5;
    let startPage, endPage;
    if (totalPages <= maxPages) {
        startPage = 1;
        endPage = totalPages;
    } else {
        const middlePage = Math.ceil(maxPages / 2);
        if (currentPage <= middlePage) {
            startPage = 1;
            endPage = maxPages;
        } else if (currentPage + middlePage >= totalPages) {
            startPage = totalPages - maxPages + 1;
            endPage = totalPages;
        } else {
            startPage = currentPage - middlePage + 1;
            endPage = currentPage + middlePage - 1;
        }
    }

    const subCategorySelect = (event, selectedStr) => {
        event.currentTarget.parentNode.classList.toggle('active');
        var selectedArr = selectedStr.split("@@@@")
        setFilterKeyword('')
        dispatch({ type: SET_SUBCATEGORY_FILTER, payload: { category: selectedArr[0], subCategory: selectedArr[1] } })
    }

    const handleSubmenClick = (event) => {
        event.currentTarget.nextElementSibling.classList.toggle('active');
    }

    const sourceChecked = (e, source) => {
        console.log("eve", e.target.checked)
        if (e.target.checked) {
            if (selectedSources.includes(source) == false) {
                // setSelectedSources([...selectedSources, source])
                dispatch({ type: SET_SOURCE_FILTER, payload: { sources: [...selectedSources, source] } })
            }
        } else {
            const filteredSources = selectedSources.filter((item) => item != source)
            dispatch({ type: SET_SOURCE_FILTER, payload: { sources: filteredSources } })
        }
    }

    const handleApply = () => {
        getProducts({ page_size: 500, page: currentPage, sub_category: selectedSubCategory, sources: selectedSources, keyword: keyword }).unwrap().then(function (res) {
            setTotalPages(res.total_pages)
            setResults(res.results)
        }).catch(function (err) {
            console.log(err)
        })
    }

    const handleFilterKey = (e) => {
        const val = e.target.value;
        if (e.key === 'Enter') {
            if (val.trim() !== '' && val.length >= 3) {
                dispatch({ type: SET_KEYWORD_FILTER, payload: { keyword: val } })
            }
            if (val.length < 3) {
                dispatch({ type: SET_KEYWORD_FILTER, payload: { keyword: '' } })
            }
        }
    }

    const handleFilterKeywordChange = (e) => {
        setFilterKeyword(e.target.value)
        const val = e.target.value;
        if (filterKeyword !== val && val.length >= 3) {
            dispatch({ type: SET_KEYWORD_FILTER, payload: { keyword: val } })
        }
        if (val.length < 3) {
            dispatch({ type: SET_KEYWORD_FILTER, payload: { keyword: '' } })
        }
    }

    const handleAddFavourite = (e) => {
        if (selectedSubCategory) {
            addFavourite({ sub_category: selectedSubCategory }).unwrap().then(function (res) {
                console.log(res)
            })
            dispatch({ type: ADD_REMOVE_FAVOURITE, payload: { selectedSubCategory } })
        }

    }

    const handleAddUnlike = (e) => {
        if (selectedSubCategory) {
            addUnlike({ sub_category: selectedSubCategory }).unwrap().then(function (res) {
                console.log(res)
            })
            dispatch({ type: ADD_REMOVE_UNLIKE, payload: { selectedSubCategory } })
        }
    }
    
    const handleAddRemoveCartOnTr = (isChecked, product) => {
        if (!isChecked) {
            addCart({ product_id: product.id }).unwrap().then(function (res) {
                console.log(res)
            })
        } else {
            removeCart({ url: product.url }).unwrap().then(function (res) {
                console.log(res)
            })
        }
        dispatch({ type: ADD_REMOVE_CART, payload: { product: product } })
    }

    const handleAddRemoveCart = (e, product) => {
        if (e.target.checked) {
            addCart({ product_id: product.id }).unwrap().then(function (res) {
                console.log(res)
            })
        } else {
            removeCart({ url: product.url }).unwrap().then(function (res) {
                console.log(res)
            })
        }
        dispatch({ type: ADD_REMOVE_CART, payload: { product: product } })
    }

    const handleCartClick = () => {
        navigate("/m/my-shopping-list")
    }

    return (<>
        <Helmet title={t('title')} />
        <header>
            <div className="top_nav">
                <div></div>
                <div className="logo">
                    <a href="https://www.100grammes.ca">
                        <img src={vertPng} alt="100grammes" />
                    </a>
                </div>
                <div className="hamburger">
                    <div className="cart-count" onClick={() => handleCartClick()}>
                        {totalCartNum}
                    </div>
                    <button className="sidebarToggler" onClick={() => setMenuShow(true)}>
                        <img src={menuSvg} alt="Menu" />
                    </button>
                </div>
            </div>
            <div className="breadcrumb">
                <ul>
                    {/* <li>
                        <a href="#">
                            {selectedCategory}
                        </a>
                    </li> */}
                    <li className="item-has-submenu">
                        <a href="javascript:void(0)" onClick={(e) => handleSubmenClick(e)}>
                            {selectedSubCategory}
                        </a>
                        <ul className={submenuShow ? "submenu active" : "submenu"}>
                            {favourites && favourites.length > 0 &&
                                <>
                                    <li className='submenu-category-name'>
                                        <a href="javascript:void(0)">
                                            <MdFavorite size={13} /> {t('My favourites')} ({favourites.length})
                                        </a>
                                    </li>

                                    {favourites.map((favourite) => (
                                        <li key={favourite} className='submenu-subcategory-name' onClick={(e) => subCategorySelect(e, "favourite@@@@" + favourite)}>
                                            <a href="javascript:void(0)">
                                                {favourite}
                                            </a>
                                        </li>

                                    ))}

                                </>
                            }

                            {categories && categories.map((category) => (
                                <>
                                    <li key={category.id} className='submenu-category-name'>
                                        <a href="javascript:void(0)">
                                            {category.name}
                                        </a>
                                    </li>
                                    {category.sub_categories && category.sub_categories.map((sub_category) => (
                                        <li key={sub_category.id} className='submenu-subcategory-name' onClick={(e) => subCategorySelect(e, category.name + '@@@@' + sub_category.name)}>
                                            <a href="javascript:void(0)">
                                                {sub_category.name}
                                            </a>
                                        </li>
                                    ))}

                                </>

                            ))}
                            {unlikes && unlikes.length > 0 &&
                                <>
                                    <li className='submenu-category-name'>
                                        <a href="javascript:void(0)">
                                            <MdThumbDown size={13} /> {t('Unlikes')} ({unlikes.length})
                                        </a>
                                    </li>

                                    {unlikes.map((unlike) => (
                                        <li key={unlike} className='submenu-subcategory-name' onClick={(e) => subCategorySelect(e, "unlike@@@@" + unlike)}>
                                            <a href="javascript:void(0)">
                                                {unlike}
                                            </a>
                                        </li>

                                    ))}

                                </>
                            }

                        </ul>
                    </li>
                </ul>
            </div>
            <div className="items-filter">
                <ul>
                    {sources && Object.keys(sources).map((key, index) => (
                        <li className="input-group-membre grocery-item" key={index}>
                            <input type="checkbox" name={`source-${key}`} id={`item-${key.toLowerCase()}`} style={{ background: sources[key] }} onChange={e => sourceChecked(e, key)} checked={selectedSources.includes(key) ? true : false} />
                            <label for={`item-${key.toLowerCase()}`} className={`source-filter-label label-${key}`} style={{ color: sources[key] }}>{key}
                            </label>
                        </li>
                    ))}
                </ul>
            </div>
        </header>
        <MobileExpandedMenu show={menuShow} onClose={() => setMenuShow(false)} />
        <div className="apps-body">
            <div className="table-filter">
                <ul>
                    <li>
                        <input className="filter-keyword-input" type="text" placeholder={t('Filter')} value={filterKeyword} onChange={(e) => handleFilterKeywordChange(e)} onTouchStart={e => handleFilterKey(e)} />
                    </li>
                    <li>
                        {unlikes.includes(selectedSubCategory) ? (
                            <a onClick={handleAddUnlike} style={{ cursor: 'pointer' }}>
                                <MdThumbDown size={20} />
                            </a>
                        ) : (
                            <a onClick={handleAddUnlike} style={{ cursor: 'pointer' }}>
                                <MdOutlineThumbDown size={20} />
                            </a>

                        )}
                    </li>
                    <li>
                        {favourites.includes(selectedSubCategory) ? (
                            <a className='text-red' onClick={handleAddFavourite} style={{ cursor: 'pointer' }}>
                                <MdFavorite size={20} />
                            </a>
                        ) : (
                            <a className='text-red' onClick={handleAddFavourite} style={{ cursor: 'pointer' }}>
                                <MdFavoriteBorder size={20} />
                            </a>

                        )}
                    </li>
                </ul>
            </div>
            <div className="product-list">
                <table className="table">
                    <tbody>
                        {results && results.length > 0 ? (
                            <Suspense fallback={<Loading />}>
                                {results.map((result) => {
                                    const isChecked = carts[result.source] && carts[result.source].filter((item) => item.url == result.url).length > 0 ? true : false
                                    return (
                                        <tr key={result.id} className={isChecked ? 'product-selected tr-' + result.source.toLowerCase() : 'tr-' + result.source.toLowerCase()} onClick={(e) => handleAddRemoveCartOnTr(isChecked, result)}>
                                            <td className="item-thumb">
                                                <img src={result.image}
                                                    alt={result.final_name} />
                                            </td>
                                            <td className="product-info">
                                                <a className="product-name" href="javascript:void(0)">
                                                    <HighLightText text={result.final_name} highlight={keyword} />
                                                    &nbsp;
                                                    <HighLightText text={result.brand ? result.brand : ''} highlight={keyword} />
                                                </a>
                                                <ul>
                                                    <li className='product-price-for-unit'>${result.final_price_per_unit_of_measure} {result.final_unit_of_measure} ou ${result.final_price_for_item}</li>
                                                    <li className='product-source'>{result.source}</li>
                                                </ul>
                                            </td>
                                            <td className={`product-first-td-${result.source.toLowerCase()}`}>
                                                <div className="input-group-membre cart-check-item">
                                                    <input type="checkbox" id={`check_row${result.id}`} onChange={(e) => handleAddRemoveCart(e, result)} checked={isChecked} />
                                                    <label for={`check_row${result.id}`}></label>
                                                </div>
                                            </td>

                                        </tr>
                                    )
                                }
                                )}
                            </Suspense>

                        ) : ('')}
                    </tbody>
                </table>
            </div>
        </div>

    </>)
}


export default connect(mapStateToProps)(MobileMembre);